@import '../../components/solutionProblems.scss';
@import '../../components/partnerList.scss';
@import '../../components/contactArea.scss';
@import '../../components/floatButton.scss';

.solution {
  &__banner {
    transition: all 1s;
  }

  &__page {
    background: #FFFFFF;

    &-main {
      justify-content: center;
    }

    &-item {
      padding: 32px 30px 40px;
      background: #FFFFFF;
      text-align: center;
      transition: all .5s;

      &:hover {
        background: #EBF2FA;
        text-decoration: none;
      }
    
      &.active {
        background: #EBF2FA;

        &.solution__page {
          &-item {
            &:after {
              content: '';
              position: absolute;
              left: 50%;
              bottom: 0;
              transform: translateX(-50%);
              display: inline-block;
              width:100px;
              height:4px;
              background:#2661E9;
            }
          }

          &-title {
            color: #2661E9;
          }
        }
      }
    }

    &-icon {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }

    &-title {
      margin-top: 20px;
      margin-bottom: 0;
      font-weight: bold;
      color: #333333;
    }
  }

  &__problems {
    padding: 88px 0 106px;
  }

  &__main {
    margin-bottom: 0;
    padding-top: 78px;
    padding-bottom: 100px;
    background: #FFFFFF;
    overflow: hidden;
  }

  &__title {
    margin-bottom: 80px;
    font-size: 36px;
    font-weight: bold;
    color: #333333;
    text-align: center;
  }

  &__item {
    &-index {
      display: flex;
      align-items: center;
      justify-content: center;
      width:83px;
      height:50px;
      margin-bottom: 30px;
      background:#2661E9;
      border-radius:20px 0px 20px 0px;
      font-size:40px;
      font-weight:bold;
      color:#FFFFFF;
    }

    &-title {
      margin-bottom: 0;
      font-weight: bold;
      color: #333333;
    }

    & + & {
      margin-top: 150px;
    }
  }

  &__partner {
    padding: 115px 0 80px;
  }
}

.house {
  &__feature {
    &-list {
      margin-top: 36px;
    }

    &-item {
      display: flex;
      justify-content: center;
      flex-direction: column;
      height: 80px;
      overflow: hidden;
      padding: 0 20px;
      background: #F5F5F5;
      color: #333333;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        height: auto;
        padding-top: 20px;
        padding-bottom: 20px;
        background: #2661E9;
        color: #FFFFFF;

        .house__feature {
          &-desc {
            display: block;
          }
        }
      }

      & + & {
        margin-top: 4px;
      }
    }

    &-title {
      margin-bottom: 0;
      font-weight: bold;
    }

    &-desc {
      display: none;
      margin-top: 16px;
      margin-bottom: 0;
      font-size: 14px;
      color: #BDD0FA;
    }

    &-main {
      margin-top: 48px;
      padding: 55px 60px 75px 151px;
    }

    &-bg {
      position: absolute;
      top: 0;
      right: -38px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-3.png) no-repeat 50% 0%/100%;
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &.hidden {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  &__function {
    margin-left: 0;
    padding: 0;
    border: 1px solid rgba($color: #CCCCCC, $alpha: .5);

    &-image {
      align-self: flex-end;
      
      img {
        width: 100%;
        padding-right: 50px;
      }
    }

    &-item {
      padding: 20px;
      border: 1px solid rgba($color: #CCCCCC, $alpha: .5);
    }

    &-icon {
      width: 38px;
      height: 38px;
      object-fit: contain;
    }

    &-title {
      margin: 24px 0 20px;
      font-weight: bold;
      color: #333333;
    }

    &-desc {
      margin-bottom: 0;
      font-size: 14px;
      line-height: 1;
    }
  }
}

.home {
  &__feature {
    &-list {
      margin-top: 36px;
    }

    &-item {
      height: 90px;
      overflow: hidden;
      padding: 35.5px 20px;
      background: #F5F5F5;
      color: #333333;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        height: auto;
        background: #2661E9;
        color: #FFFFFF;
      }

      & + & {
        margin-top: 10px;
      }
    }

    &-title {
      margin-bottom: 0;
      font-weight: bold;
    }

    &-desc {
      margin-top: 32px;
      margin-bottom: 0;
      font-size: 14px;
      color: #BDD0FA;
    }

    &-main {
      padding: 58px 58px 128px 158px;
    }

    &-bg {
      position: absolute;
      top: 0;
      right: -42px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-3.png) no-repeat 50% 0%/100%;
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &.hidden {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  &__manage {
    margin: 88px 0 0;
    border: 1px solid rgba($color: #CCCCCC, $alpha: .5);

    &-item {
      display: flex;
      align-items: center;
      padding: 45px 25px;
      border: 1px solid rgba($color: #CCCCCC, $alpha: .5);
    }

    &-icon {
      width: 36px;
      height: 36px;
      margin-right: 20px;
      object-fit: contain;
    }

    &-main {
      flex: 1;
    }

    &-title {
      margin-bottom: 20px;
      font-weight: bold;
      color: #333333;
      line-height: 1;
    }

    &-desc {
      margin-bottom: 0;
      font-size: 14px;
      line-height: 1;
    }
  }

  &__process {
    margin-top: 55px;

    &-item {
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;

      &.active {
        .home__process {
          &-index {
            background: #2661E9;
            border-color: #2661E9;
            color: #FFFFFF;
          }
        }
      }

      & + & {
        margin-top: 30px;

        &:before {
          content: '';
          position: absolute;
          top: -30px;
          left: 18px;
          display: inline-block;
          width:1px;
          height:30px;
          background: #BABBBC;
        }
      }
    }

    &-index {
      display: flex;
      align-items: center;
      justify-content: center;
      width:37px;
      height:37px;
      margin-right: 22px;
      border:1px solid #666666;
      border-radius:50%;
      font-size: 14px;
      font-weight: bold;
      transition: all .5s;
    }

    &-text {
      flex: 1;
      margin-bottom: 0;
      font-weight: bold;
      color: #333333;
    }

    &-main {
      margin-left: 0;
      margin-top: -30px;
    }

    &-image {
      padding: 0 5px;
      
      img {
        width: 100%;
        margin-top: 5px;
        margin-bottom: 5px;
        object-fit: cover;
        cursor: pointer;
        transition: all .5s;

        &.active {
          // border:1px solid #BFBFBF;
          box-shadow:0 15px 24px 0 rgba(153,153,153,0.6);
          transform: scale(1.02);
        }
      }

      &-1 {
        height:278px;
      }

      &-2 {
        height:200px;
      }

      &-3 {
        height:200px;
      }

      &-4 {
        height:135px;
      }

      &-5 {
        height:135px;
      }
    }
  }
}

.company {
  &__feature {
    &-list {
      margin-top: 36px;
    }

    &-item {
      height: 90px;
      overflow: hidden;
      padding: 35.5px 20px;
      background: #F5F5F5;
      color: #333333;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        height: auto;
        background: #2661E9;
        color: #FFFFFF;
      }

      & + & {
        margin-top: 10px;
      }
    }

    &-title {
      margin-bottom: 0;
      font-weight: bold;
    }

    &-desc {
      margin-top: 32px;
      margin-bottom: 0;
      font-size: 14px;
      color: #BDD0FA;
    }

    &-main {
      margin-top: 45px;
      padding: 46px 30px 77px 90px;
    }

    &-bg {
      position: absolute;
      top: -18px;
      right: -60px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 50% 0%/100%;
    }

    &-media {
      position: relative;
      width: 100%;
      // box-shadow:0 15px 24px 0 rgba(153,153,153,0.6);

      video {
        width: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &.hidden {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  &__design {
    &-main {
      margin-top: 38px;
      padding: 42px 50px 81px 0;
    }

    &-bg {
      position: absolute;
      top: 28px;
      left: -44px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 50% 0%/100%;
      transform: rotate(180deg);
    }

    &-media {
      position: relative;
      width: 100%;
      // box-shadow:0 15px 24px 0 rgba(153,153,153,0.6);

      video {
        width: 100%;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &.hidden {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    
    &-list {
      margin: 38px 0 0;
    }

    &-item {
      padding: 4px;
    }
    
    &-title {
      display: flex;
      align-items: center;
      height: 90px;
      margin-bottom: 0;
      padding: 0 20px;
      font-weight: bold;
      line-height: 1;
      background: #F5F5F5;
      color: #333333;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        background: #2661E9;
        color: #FFFFFF;
      }
    }

    &-desc {
      margin-top: 32px;
      margin-bottom: 0;
      font-size: 14px;
      color: #BDD0FA;
    }
  }
}


@media (max-width: 1199px) {
  .house__feature-bg,
  .home__feature-bg,
  .company__feature-bg,
  .company__design-bg {
    display: none;
  }
}

@media (max-width: 991px) {
  .house {
    &__function {
      margin-top: 20px;
    }
  }

  .home {
    &__process {
      &-main {
        margin-top: 20px;
      }
    }
  }
}

@media (max-width: 767px) {
  
  .home {
    &__feature {
      &-main {
        margin: 20px 0;
        padding: 20px 66px 54px;
      }
    }
  }
}